import React from 'react'
import Context from '../context';

function AddTodo({classes}) {

    const [value, setValue] = React.useState('');

    const { addTodo, hideModal } = React.useContext(Context)

    function submitHandler(event) {
        event.preventDefault()

        if (value.trim()) {
            addTodo(value)
            setValue('')
        }
    }

    return <div className={classes.join(' ')}>
               <div className="modal-hide" onClick={event => hideModal()}></div>
               <form className="modal" onSubmit={submitHandler}>
                   <h1>Create good</h1>
                   <input className="create-input" value={value} onChange={event => setValue(event.target.value)} />
                   <div className="group-btn">
                       <button className="create-btn" type="submit">Add good</button>
                       <button className="create-btn" type="reset" onClick={event => hideModal()}>Cancel</button>
                   </div>
               </form>
           </div>
}

export default AddTodo