import React from 'react'
import PropTypes from 'prop-types'
import Context from '../context'

function TodoItem({todo, index}) {

    let classes = []

    const { removeTodo, toggleTodo } = React.useContext(Context)

    if(todo.comlited) {
        classes.push('done')
    }

    return <li className="delete-elem">
               <span className={classes.join('')}>
                   <input id={todo.id} type='checkbox' onChange={toggleTodo.bind(null, todo.id)} checked={todo.comlited} />
                   <label htmlFor={todo.id} />
                   &nbsp;
                   <strong>{++index})</strong>&nbsp;
                   {todo.title}
                   &nbsp;
               </span>
               <button className="delete-btn" onClick={removeTodo.bind(null, todo.id)} >&times;</button>
           </li>
}

TodoItem.propTypes = {
    index: PropTypes.number,
    todo: PropTypes.object.isRequired
}

export default TodoItem