import React from 'react'
import Context from './context'
import AddTodo from './components/AddTodo'
import TodoList from './components/TodoList'

function App() {

    const [classes, changeClass] = React.useState(['modal-wrapper'])

    const [todos, setTodos] = React.useState(localStorage.getItem('date') ? JSON.parse(localStorage.getItem('date')) : [])

    function toggleTodo(id) {
        setTodos(
            todos.map(todo => {
                if(todo.id === id) {
                    todo.comlited = !todo.comlited
                }
                return todo
            })
        )
    }

    function removeTodo(id) {
        setTodos(
            todos.length !== 1 ? todos.filter(todo => todo.id !== id) : []
        )
    }

    function addTodo(title) {
        setTodos(
            todos.concat([{
                title,
                id: Date.now(),
                comlited: false
            }])
        )
        hideModal()
    }

    function deleteAllTodos() {
        setTodos([])
        localStorage.clear()
    }

    function openModal() {
        changeClass( classes.concat('active') )
    }

    function hideModal() {
        changeClass( ['modal-wrapper'] )
    }

    localStorage.setItem('date', JSON.stringify(todos))

    return (
            <Context.Provider value={{removeTodo, toggleTodo, addTodo, hideModal}}>
                {
                    todos.length ?
                    ( <div className="App">
                        <h1>Your goods list</h1>
                        <TodoList todos={todos}/>
                        <div className="group-btn">
                            <button className="create-btn" onClick={event => deleteAllTodos()}>Delete all goods</button>
                            <button className="create-btn" onClick={event => openModal()}>Add new good</button>
                        </div>
                    </div> ) :
                    ( <div className="App">
                        <h1>Add in goods list</h1>
                        <div className="group-btn">
                            <button className="create-btn" onClick={event => openModal()}>Add new good</button>
                        </div>
                    </div> )
                }
                <AddTodo classes={classes} />
            </Context.Provider>
        );
    }

export default App;
